import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';

import DefaultLayout from '../../assets/layout/DefaultLayout';
import MainContent from '../../components/MainContent';
import jokesActions from '../../redux/actions/jokes';
import request from '../../api/requests/jokes.request';
import { RandomJokeModel } from '../../utils/models/JokeModel';

interface propTypes {
  match: {
    params: {
      category: string
    }
  }
}

const Jokes: React.FC<propTypes> = ({ match }) => {
  const {
    params: { category },
  } = match;

  const dispatch = useDispatch();

  useEffect(() => {
    const getCategories = async () => {
      try {
        const newJokes: RandomJokeModel[] = await request.getJokes(category);

        if (newJokes)
          dispatch(jokesActions.setJokes(newJokes));
      } catch (error) {
        // eslint-disable-next-line no-console
        console.error(error);
      }
    }

    getCategories();
  }, [dispatch]);

  return (
    <DefaultLayout>
      <MainContent category={category} />
    </DefaultLayout>
  );
};

export default Jokes;
