import React, { useEffect, useState } from 'react';
import { FaAngleDoubleLeft, FaAngleDoubleRight } from 'react-icons/fa';

import { RandomJokeModel } from '../../utils/models/JokeModel';
import SavedCard from '../SavedCard';

type CardProps = {
  jokes: RandomJokeModel[];
  carouselPostMargin: number;
  carouselPostWidth: string;
  autoPlay: boolean;
  delay: number;
}

const Carousel: React.FC<CardProps> = (props: CardProps) => {

  const {jokes, carouselPostMargin, carouselPostWidth, autoPlay, delay} = props;

  const [currentIndex, setCurrentIndex] = useState(0);
  let timer: number;

  const conputedLeft = () => {
    const leftSpan = parseInt(`${-currentIndex * parseInt(carouselPostWidth, 10)}`, 10)
    return{
      left: carouselPostWidth.toString().match(/[%vw]/) != null ?
      `calc(${leftSpan}% - ${carouselPostMargin * 1 *currentIndex}px)`:
      `${leftSpan - (carouselPostMargin * 1 *currentIndex)}px`
    }
  }

  const handleNext = () => {
    setCurrentIndex((currentIndex + 1 +  jokes.length) % jokes.length);
  }

  const handlePrev = () => {
    setCurrentIndex((currentIndex - 1 +  jokes.length) % jokes.length);
  }

  const autoRun = () => {
    timer = window.setInterval(() => {
      handleNext()
    }, delay * 1000);
  }

  useEffect(() => {
    if (autoPlay)
      autoRun()

    return () => {
      clearInterval(timer);
    }
  }, [currentIndex]);

  return (
    <div className='carousel'>
      <div className='carousel__info'>
        <p>{currentIndex + 1} / {jokes.length}</p>
      </div>
      <div className='carousel__container'>
        <div className="carousel__area">
          <div style={conputedLeft()} className="carousel__posts">
            {/* eslint-disable */}
            {jokes.map((joke) =>
              (
                <div
                  key={joke._id}
                  style={{
                    margin:`0px ${carouselPostMargin}px `,
                  }}
                  className="carousel__postBox">
                  <SavedCard joke={joke} />
                </div>
              )
            )}
            {/* eslint-enable */}
          </div>
        </div>

        <div className="carousel__actions">
          {/* eslint-disable-next-line */}
          <div onClick={handlePrev} className="control-left">
            <FaAngleDoubleLeft />
          </div>

          {/* eslint-disable-next-line */}
          <div onClick={handleNext} className="control-right">
            <FaAngleDoubleRight />
          </div>
        </div>
      </div>
    </div>
  )

};

export default Carousel;
