import { AppDispatch, AppThunk } from '../store';
import { actions } from '../slices/jokes';
import { RandomJokeModel } from '../../utils/models/JokeModel';

const jokesActions = {
  resetState: (): AppThunk => async (dispatch: AppDispatch): Promise<void> => {
    dispatch(
      actions.getInitialize()
    );
  },
  setJokes:
    (jokes: RandomJokeModel[]): AppThunk =>
    async (dispatch: AppDispatch): Promise<void> => {
      dispatch(
        actions.setJokes({
          jokes
        })
      );
    },
  addSavedJokes:
    (newJokes: RandomJokeModel[], savedJokes: RandomJokeModel[]): AppThunk =>
    async (dispatch: AppDispatch): Promise<void> => {
      // Remove duplicated seen jokes
      const uniqueSavedJokes: RandomJokeModel[] = [...new Set(savedJokes)];
      const uniqueNewJokes: RandomJokeModel[] = [...new Set(newJokes)];

      uniqueNewJokes.forEach((newJoke) => {
        const result: RandomJokeModel[] = uniqueSavedJokes.filter(
          (savedJoke) => savedJoke.url === newJoke.url
        );

        if (result.length === 0) {
          uniqueSavedJokes.push(newJoke);
        }
      });

      dispatch(
        actions.addSavedJokes({
          savedJokes: uniqueSavedJokes
        })
      );
    },
}

export default jokesActions;
