import { useSelector } from 'react-redux';

import { RootState } from '../store';

export default function useJokes() {
  const {
    isLoading,
    jokes,
    savedJokes,
    errorMessage,
  } = useSelector((state: RootState) => state.jokes);

  return {
    isLoading,
    jokes,
    savedJokes,
    errorMessage,
  };
}
