import React from 'react';
import { FaLaughBeam } from 'react-icons/fa';

import { RandomJokeModel } from '../../utils/models/JokeModel';

type CardProps = {
  joke: RandomJokeModel;
}

const AnimationCard1: React.FC<CardProps> = (props: CardProps) => {

  const { joke } = props;

  return (
    <div className='animation-card1'>
      <header className='animation-card1__header'>
        <FaLaughBeam />
        {/* eslint-disable-next-line */}
        <span>{joke._id}</span>
        <img alt='icon' src={joke.icon_url} />
      </header>
      <div className='animation-card1__body'>
        <p className='animation-card1__text'>
          “{joke.value}”
        </p>
      </div>
    </div>
  )
};

export default AnimationCard1;
