import React from 'react';
import { RandomJokeModel } from '../../utils/models/JokeModel';

type CardProps = {
  joke: RandomJokeModel;
}

const SavedCard: React.FC<CardProps> = (props: CardProps) => {

  const { joke } = props

  return (
    <div className='saved-card'>
      <div className='saved-card__main'>
        <img
          alt="icon"
          src={joke.icon_url}
          className="saved-card__image"
          />
        <div className='saved-card__info'>
          <p className='saved-card__value'>{joke.value}</p>
        </div>
      </div>
    </div>
  )

};

export default SavedCard;
