import React from 'react';
import { RandomJokeModel } from '../../utils/models/JokeModel';

type CardProps = {
  joke: RandomJokeModel;
}

const AnimationCard3: React.FC<CardProps> = (props: CardProps) => {

  const { joke } = props

  return (
    <div className="card3">
      <div className="card3__icon">
        <img alt="icon" src={joke.icon_url} />
      </div>
      <div className="card3__value">
        <p>{joke.value}</p>
      </div>
      <div className="card3__bar">
        <div className="emptybar" />
        <div className="filledbar" />
      </div>
    </div>
  )

};

export default AnimationCard3;
