import axios from "axios";
import axiosInstance from '../axios/setupAxios';
import { RandomJokeModel } from '../../utils/models/JokeModel';

const request = {
  getJokes: async (category: string): Promise<RandomJokeModel[]> => {
    const results = await axios.all([
      axiosInstance.get(`jokes/random?category=${category}`),
      axiosInstance.get(`jokes/random?category=${category}`),
      axiosInstance.get(`jokes/random?category=${category}`),
      axiosInstance.get(`jokes/random?category=${category}`),
      axiosInstance.get(`jokes/random?category=${category}`)
    ]);

    const randomJokes: RandomJokeModel[] = [];
    results.forEach((result: {data: RandomJokeModel}, index) => {
      randomJokes.push({
        'id': index.toString(),
        '_id': result.data.id,
        'categories': result.data.categories,
        'created_at': result.data.created_at,
        'icon_url': result.data.icon_url,
        'updated_at': result.data.updated_at,
        'url': result.data.url,
        'value': result.data.value
      });
    });

    return randomJokes;
  },
};

export default request;
