import React from 'react';
import { RandomJokeModel } from '../../utils/models/JokeModel';

type CardProps = {
  joke: RandomJokeModel;
}

const AnimationCard2: React.FC<CardProps> = (props: CardProps) => {

  const { joke } = props

  return (
    <div className="carousel__item">
      {/* eslint-disable-next-line */}
      <div className="carousel__item-head">
        <img alt="icon" src={joke.icon_url} />
      </div>
      <div className="carousel__item-body">{joke.value}</div>
    </div>
  )

};

export default AnimationCard2;
